import React, { useState, useEffect } from 'react';
import { Container, Card, Row, Col } from 'react-bootstrap';

function News() {
    const url = 'https://newsapi.org/v2/top-headlines?country=ua&apiKey=8e1c62073aa54a87927e9e5c5a52cf48';

    const [error, setError] = useState(null);
    const [isLoaded, setLoading] = useState(false);
    const [items, setItems] = useState([]);

    useEffect(() => {
        setLoading(true);
        fetch(url)
            .then(res => res.json())
            .then(
                (result) => {
                    setItems(result.articles);
                })
            .catch((err) => {
                setError(err);
            })
            .finally(() => {
                setLoading(false);
            });
    }, [])

    // function newsFetch() {
    //     items.map((item) => {
    //         console.log(item);
    //     })
    // }

    const renderCard = (card, key) => {
        return (
            <Col className="col-lg-4 d-flex align-items-stretch" key={key}>
                <Card border="light" className="myCard">
                    <Card.Img variant="top" className="cardImg" src={card.urlToImage} />
                    <Card.Body>
                        <Card.Title>{card.title}</Card.Title>
                        <Card.Text>
                            {card.description}
                        </Card.Text>
                    </Card.Body>
                    <Card.Footer className="text-muted cardFooter">{card.author}</Card.Footer>
                </Card>
            </Col>
        )
    }

    if (error) {
        return <div>Error: {error.message}</div>;
    }

    if (isLoaded) {
        return <div>Loading...</div>;
    }

    return (
        <Container fluid>
            <Row>
                {items.map(renderCard)}
            </Row>
            {/* <Button variant="outline-info mt-3 mb-3" onClick={newsFetch}>News</Button> */}
        </Container >
    );

}
export default News;